<template>
  
  <header class="header">  
    <img src="./assets/logo.png" alt="" class="logo" @click="step=3">   
    <button class="header-button-left" @click="step=0">Cancel</button> 
    <button v-if="step == 1" class="header-button-right" @click="step=2">Next</button> 
    <button v-if="step == 2" class="header-button-right" @click="publish">글등록</button> 
  </header>  
  <Container @write="작성글=$event" :게시물="게시물" :step="step" :업로드이미지="업로드이미지"/>
  
  <button v-if="step==0" @click="moreData">게시물 더보기</button>  

  <footer class="footer">  
    <div v-if="step == 0" class="footer-button-plus">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile">
      <label for="file" class="input-plus"> + </label>
    </div>  
  </footer>
</template>

<script>
  import postdata from './assets/postdata'
  import Container from './components/Container.vue'
  import axios from 'axios' 
  import {ref, reactive, onMounted, getCurrentInstance} from 'vue' 
  
  export default {
    name: 'App',

    setup(){
      let 게시물 = reactive(postdata);
      let step =  ref(0);
      let 업로드이미지  = ref('');
      let 작성글 = ref('');
      let 저장할필터 = ref('');
      
      const internalInstance = getCurrentInstance(); 
      const emitter = internalInstance.appContext.config.globalProperties.emitter;

      onMounted(() => {
        emitter.on('필터적용신호', (a) => {
          저장할필터.value = a;
        });
      });
      
      let moreData = () => {        
        axios.get('https://tarolong.github.io/more0.json').then((res) => {
          // console.log(res.data);
          게시물.push(res.data)
        });
      };

      let upload = (event) => {
        // console.log(event.target.files);
        let temp = event.target.files[0]
        let url = URL.createObjectURL(temp);
        업로드이미지.value = url;
        step.value = 1;
      };

      let publish = () => {
        let 새거 = {
          name: "Kim Hyun",
          userImage: "https://placeimg.com/100/100/arch",
          postImage: 업로드이미지.value,
          likes: 36,
          date: "May 15",
          liked: false,
          content:  작성글.value,
          filter: 저장할필터.value
        };        
        게시물.unshift(새거);
        step.value = 0;
      }

      return {
        게시물,
        step,
        업로드이미지,
        작성글,
        저장할필터,
        moreData,
        upload,
        publish
      }
    },

    components: {    
      Container
    }
  }
</script>

