<template>
    <div style="padding : 10px">
        <h4> Follwer </h4>

        <input placeholder="🔍" @input="search($event.target.value)"/>

        <div class="post-header" v-for="(item, index) in follower" :key="index">
            <div class="profile" :style="{backgroundImage: `url(${item.image})`}"></div>
            <span class="profile-name">{{ item.name }}</span>
        </div>

    </div>
</template>

<script>
import {onMounted, ref} from 'vue'
import axios from 'axios'

export default {
    name: 'MyPage',

    setup() { 

        let follower = ref([]);
        let followerOrgin = ref([]);

        onMounted( () => {
            axios.get('https://tarolong.github.io/sns.json').then( (res) => {
                follower.value = res.data;
                followerOrgin.value = [...res.data];
            });
        });

        let search = (word) => {
           let temp = followerOrgin.value.filter((obj) => {
               return obj.name.indexOf(word) != -1
           });

           follower.value = [...temp];
        }
        
        return {       
            follower,
            search
        }
    }

}
</script>

