<template>
    
    <div class="post">        
        <div class="post-header">
            <div class="profile" :style="{ backgroundImage: `url( ${내용물.userImage} )` }" ></div>
            <span class="profile-name">{{내용물.name}}</span>
        </div>
        <div class="post-body" :class="내용물.filter" :style="{ backgroundImage: `url(${내용물.postImage})`}"></div>
        <div class="post-content">            
            <p @click="$store.commit('좋아요')"> {{ likes }} Likes</p>            
            <p><strong>{{내용물.name}}</strong> {{내용물.content}}</p>
            <p class="date">{{내용물.date}}</p>
        </div>
    </div>
</template>
<script>
    import { computed, toRefs } from 'vue'
    import { useStore } from 'vuex'

    export default {
        name: 'Post',
        // props: {
        //    게시물: Object
        // },
        props: ['게시물'],
        setup(props) {
            let { 게시물 } = toRefs(props);
            let 내용물 = 게시물.value;
            let store = useStore();
            let likes = computed( () => {
                return store.state.likes
            });

            return {
                내용물,
                likes
            }
        }
    }
</script>
<style>
    .post {
        position: relative;
        width: 100%;
        margin-bottom: 50px;
    }

    .profile {
        background-image: url("https://placeimg.com/100/100/arch");
        width: 30px;
        height: 30px;
        background-size: 100%;
        border-radius: 50%;
        float: left;
    }

    .profile-name {
        display: block;
        float: left;
        padding-left: 10px;
        padding-top: 7px;
        font-size: 14px;
    }

    .post-header {
        height: 50px;
        padding: 10px;
    }

    .post-body {
        background-image: url("https://placeimg.com/640/480/people");
        height: 450px;
        background-position: center;
        background-size: cover;
    }

    .post-content {
        padding-left: 15px;
        padding-right: 15px;
        font-size: 14px;
    }
    .post-content p {
        padding: 10px  0;
    }

    .date {
        font-size: 11px;
        color: grey;
        margin-top: -8px;
    }
</style>