<template>
  <div @click="fire" class="filter-item" :class="효과" :style="{backgroundImage: `url(${원본})`}">      
      <slot name="번호"></slot>
      <slot name="이름"></slot>
      <button >필터적용</button>
  </div>
</template>

<script>
import { toRefs, getCurrentInstance  } from 'vue';
export default {
    name: 'FilterBox',
    // props: {
    //     이미지:String,
    //     필터:String
    // },
    props: {
        이미지: { type:String, require: true },
        필터: { type:String, require: true },
    },
    setup(props) {

        let { 이미지, 필터 } = toRefs(props);
        let 원본 = 이미지.value;
        let 효과 = 필터.value;
        const internalInstance = getCurrentInstance(); 
        const emitter = internalInstance.appContext.config.globalProperties.emitter;

        let fire = () => {
            emitter.emit('필터적용신호', 효과);
        }
        
        return {
            원본,
            효과,
            fire
        }
    }
}
</script>

<style>
    .filter-item{
        position: relative;
        display: inline-block;
        width: 100px;
        height: 100px;
        margin: 10px 10px 10px auto;
        padding: 10px;
        background-size: cover;
    }
    .filter-item span {
        display: block;
        background: rgba(0, 0, 0, 0.2);
        color: #fff;
        text-align: center;
    }
</style>