<template>
   <div class="container"> 
        <!-- POST 목록 영역  -->
        <div v-if="단계 == 0">
            <Post v-for="(item, index) in 내용물" :key="index" :게시물="item" />
        </div>
        <!-- 이미지 업로드 영역 -->
        <div v-if="단계 == 1">
            <div class="upload-image" :class="선택한필터" :style="{backgroundImage: `url(${새이미지})`}"></div>
            <div class="filters">
                <FilterBox v-for="(item, index) in 필터들" :key="index"  :이미지="새이미지" :필터="item">
                    <template v-slote:번호>
                        <span>{{index}} 번째 필터</span>
                    </template>
                    <template v-slot:이름>
                        <span>{{item}}</span>
                    </template>                    
                </FilterBox>
            </div>
        </div>
        <!-- POST 등록 영역 -->
        <div v-if="단계 == 2">
            <div class="upload-image" :class="선택한필터" :style="{backgroundImage: `url(${새이미지})`}"></div>
            {{선택한필터}}
            <div class="write">
                <textarea @input="writeFN" class="write-box">여기에 글자를 입력해주세요</textarea>
            </div>
        </div>
        <!-- MyPage 영역 -->
        <div v-if="단계 == 3">
            <MyPage />            
        </div>
   </div>  

</template>

<script>
import {toRefs, onMounted, getCurrentInstance, computed, ref } from 'vue'

import Post from './Post.vue'
import FilterBox from './FilterBox.vue'
import MyPage from './MyPage.vue'

export default {
    name: 'Container',
    // props: {
    //     게시물: Array,
    //     step: Number,
    //     업로드이미지: String
    // },
    props: ['게시물', 'step', '업로드이미지'],
    setup(props, { emit }) {

        let writeFN = (e) => {
            emit('write', e.target.value)
        }

        let { 게시물, step, 업로드이미지 } = toRefs(props);
        let 내용물 = 게시물;
        let 단계 =  step;
        let 단계값 = computed(()=>{          
            return 단계;
        });
        let 새이미지 = 업로드이미지;    
        let 필터들 = ["aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson",
                    "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua",
                    "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"
            ]; 

        let 선택한필터 =  ref('');


        const internalInstance = getCurrentInstance(); 
        const emitter = internalInstance.appContext.config.globalProperties.emitter;

        onMounted( ()=>{
            emitter.on('필터적용신호', (a) => {                
                선택한필터.value = a;
            });
        });

        return {
            내용물,
            단계,
            새이미지,
            필터들,
            선택한필터,
            단계값,
            writeFN
        }
    },

    
    components: {
        Post,
        FilterBox,
        MyPage
    }
}
</script>

<style>
    .upload-image {
        position: relative;
        display: block;
        height: 450px;
        background: green;
        background-size: cover;
    }
    .filters {
        position: relative;
        display: block;
        overflow-x: scroll;
        white-space: nowrap;
    }
    .filter-1 {
        display: inline-block;
        width: 100px;
        height: 100px;
        margin: 10px;
        padding: 10px;        
        background: orange;
        background-size: cover;
    }

    .filters::-webkit-scrollbar { height: 5px }
    .filters::-webkit-scrollbar-track { background: #f1f1f1 }
    .filters::-webkit-scrollbar-thumb { background: #888; border-radius: 10px; }
    .filters::-webkit-scrollbar-thumb:hover { background: #555 }

    .write-box {
        position: relative;
        display: block;
        border: none;
        width: 90%;
        height: 100px;
        margin: 10px auto;
    }
</style>